@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Regular.woff2"), url("../fonts/ProximaNova-Regular.woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Semibold.woff2"), url("../fonts/ProximaNova-Semibold.woff");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Bold.woff2"), url("../fonts/ProximaNova-Bold.woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Extrabld.woff2"), url("../fonts/ProximaNova-Extrabld.woff");
  font-weight: 800;
  font-style: normal;
}
@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-RegularIt.woff2"), url("../fonts/ProximaNova-RegularIt.woff");
  font-weight: 400;
  font-style: italic;
}
* {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}
fieldset {
  border: none;
  padding: 0;
  margin: 0;
}
label {
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 13px;
  margin: 30px auto 10px;
  display: block;
}
select {
  appearance: none;
  background: url('../img/svg/arrow.svg') no-repeat 95%;
  outline: 0;
}
.copyright {
  background: #eeeeee;
  padding: 12px 0;
  color: #9d9c9c;
  font-family: "Proxima", Arial, sans-serif;
}
@media (max-width: 767px) {
  .copyright {
    padding-bottom: 65px;
    font-size: 12px;
  }
}
.container {
  width: 1040px;
  margin: 0px auto;
  padding: 0px 20px;
}
@media (max-width: 1199px) {
  .container {
    width: 710px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 280px;
    padding: 0px 5px;
  }
}
.top-wrap__container {
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: space-between;
}
.top-wrap__list {
  display: flex;
  list-style: none;
  padding: 0;
  width: 130px;
  height: 35px;
  border: 2px solid #c2c2c2;
}
.top-wrap__item {
  width: 25%;
  height: 100%;
}
.top-wrap__item:not(:last-child) {
  border-right: 2px solid #c2c2c2;
}
.top-wrap__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.top-wrap__link svg {
  height: 15px;
}
.top-wrap__city {
  border: none;
  appearance: none;
  background: url(../img/svg/arrow.svg) no-repeat right;
  background-size: 14%;
  width: 85px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  color: #1f1f1f;
  outline: none;
}
.top-wrap__city-wrap,
.top-wrap__phone-wrap,
.top-wrap__user-wrap,
.top-wrap__basket-wrap {
  position: relative;
  padding-left: 20px;
}
.top-wrap__city-wrap svg,
.top-wrap__phone-wrap svg,
.top-wrap__user-wrap svg,
.top-wrap__basket-wrap svg {
  position: absolute;
  height: 16px;
  left: 0;
  top: 1px;
}
.top-wrap__phone,
.top-wrap__user,
.top-wrap__basket {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  color: #1f1f1f;
  text-decoration: none;
}
.top-wrap__user:hover,
.top-wrap__basket:hover {
  border-bottom: 1px dashed #888888;
}
.top-wrap__basket-wrap {
  position: relative;
  padding-right: 35px;
}
.top-wrap__basket-wrap::after {
  content: '3';
  display: block;
  width: 15px;
  height: 20px;
  color: #bd8b53;
  font-family: 'PTSerif', serif;
  font-size: 18px;
  border-left: 1px solid #bd8b53;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 5px;
}
@media (max-width: 1199px) {
  .top-wrap__city,
  .top-wrap__phone,
  .top-wrap__user,
  .top-wrap__basket {
    font-size: 14px;
  }
  .top-wrap__city {
    width: 70px;
  }
}
@media (max-width: 767px) {
  .top-wrap {
    position: fixed;
    bottom: 0;
    background: #fff;
    width: 100%;
    z-index: 5;
    border-top: 1px solid #000;
  }
  .top-wrap__list,
  .top-wrap__phone-wrap {
    display: none;
  }
  .top-wrap__basket,
  .top-wrap__city,
  .top-wrap__phone,
  .top-wrap__user {
    font-size: 12px;
  }
}
.header {
  background: #1f1f1f;
  height: 60px;
}
.header__wrap,
.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__nav-list {
  padding: 0;
  list-style: none;
  width: 700px;
}
.header__nav-link {
  color: #fff;
  text-transform: uppercase;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  text-decoration: none;
}
.header__nav-link:hover {
  border-bottom: 1px dashed #888888;
}
@media (max-width: 1199px) {
  .header__wrap img {
    width: 100px;
  }
  .header__nav-link {
    font-size: 12px;
  }
  .header__nav-list {
    width: 470px;
  }
}
@media (max-width: 767px) {
  .header__wrap {
    display: block;
  }
  .header__wrap > a {
    display: block;
    margin: auto;
    width: 110px;
  }
  .header__nav-link {
    font-size: 10px;
  }
  .header {
    height: auto;
    padding: 10px 0;
  }
  .header__nav-list {
    width: 100%;
    margin: 15px 0 0;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }
  .header__nav-list li {
    width: 50%;
  }
}
.greetings {
  height: 450px;
  background: url("../img/jpg/greetings.jpg") no-repeat;
  background-size: cover;
}
.greetings__slogan {
  font-family: "Proxima";
  font-weight: 700;
  font-size: 48px;
  color: #fff;
  margin: 0;
  text-align: center;
  padding: 30px 0;
  position: relative;
}
.greetings__slogan::before,
.greetings__slogan::after {
  content: '';
  width: 165px;
  height: 4px;
  background: #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.greetings__slogan::before {
  top: 0;
}
.greetings__slogan::after {
  bottom: 0;
}
.greetings__slogan--small {
  font-family: "Proxima";
  font-weight: 400;
}
.greetings__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  position: relative;
}
.greetings__pic {
  position: absolute;
  right: 0;
  bottom: 0;
}
@media (max-width: 1199px) {
  .greetings__slogan {
    z-index: 1;
  }
  .greetings__pic {
    right: -25px;
  }
}
@media (max-width: 767px) {
  .greetings__slogan {
    font-size: 25px;
    filter: drop-shadow(0px 0px 2px #000);
  }
  .greetings {
    height: 300px;
  }
  .greetings__pic {
    width: 90%;
    right: 0;
  }
}
.range__list,
.range__bill-list {
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.range__btn {
  border: 2px solid transparent;
  background: transparent;
  font-family: "Proxima";
  font-weight: 800;
  font-size: 14px;
  cursor: pointer;
  padding: 15px;
  text-transform: uppercase;
  height: 45px;
}
.range__btn:hover {
  color: #b98348;
  border: 2px solid #b98348;
}
.range__bill-list {
  flex-wrap: wrap;
}
.range__bill-item {
  width: 33%;
  height: 328px;
  margin-bottom: 5px;
}
.range__bill-item article {
  width: 100%;
  height: 100%;
  background-size: cover;
}
.range__bill-item:nth-child(1) article {
  background: url("../img/jpg/range1.jpg") no-repeat;
}
.range__bill-item:nth-child(2) article {
  background: url("../img/jpg/range2.jpg") no-repeat;
}
.range__bill-item:nth-child(3) article {
  background: url("../img/jpg/range3.jpg") no-repeat;
}
.range__bill-item:nth-child(4) article {
  background: url("../img/jpg/range4.jpg") no-repeat;
}
.range__bill-item:nth-child(5) article {
  background: url("../img/jpg/range5.jpg") no-repeat;
}
.range__bill-item:nth-child(6) article {
  background: url("../img/jpg/range6.jpg") no-repeat;
}
.first-bill-header,
.first-bill-desc,
.first-bill-text,
.fourth-bill-text {
  font-family: "Proxima", Arial, sans-serif;
  color: #fff;
  text-transform: uppercase;
}
.first-bill-title {
  text-align: center;
  position: absolute;
  top: 50%;
  margin: 0;
}
.first-bill-header {
  font-weight: 700;
  font-size: 22px;
}
.first-bill-desc {
  font-size: 22px;
  font-weight: 400;
  font-family: serif;
}
.first-bill-text {
  font-size: 12px;
  margin-top: 20px;
  display: block;
}
.first-bill,
.second-bill,
.third-bill,
.fourth-bill,
.fifth-bill,
.sixth-bill {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
}
.third-bill-text {
  color: #fff;
  background: rgba(195, 143, 102, 0.6);
  padding: 15px 20px;
  width: 90%;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
}
.third-bill-text {
  position: absolute;
  top: 40%;
}
.fourth-bill {
  flex-direction: column;
}
.fourth-bill-text {
  text-align: center;
  font-size: 22px;
}
.fourth-bill-italic {
  font-family: serif;
  font-weight: 400;
  margin-top: 15px;
  display: block;
}
.fourth-bill-decor {
  position: relative;
}
.fourth-bill-decor::before,
.fourth-bill-decor::after {
  content: '';
  display: block;
  position: absolute;
  width: 40px;
  height: 1px;
  background: #fff;
  top: 50%;
}
.fourth-bill-decor::before {
  left: -45px;
}
.fourth-bill-decor::after {
  right: -45px;
}
.fourth-bill-link {
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border: 2px solid #fff;
  padding: 20px;
}
.fifth-bill-desc {
  text-transform: uppercase;
  color: #fff;
  font-family: 'Proxima', Arial, sans-serif;
  font-weight: 700;
  text-align: center;
}
.fifth-bill-text {
  font-size: 22px;
}
.fifth-bill-name-text {
  font-family: Serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 2;
}
.fifth-bill-last-text {
  font-size: 12px;
}
.sixth-bill-text {
  color: #fff;
  text-transform: uppercase;
  font-size: 22px;
  font-family: 'Proxima', Arial, sans-serif;
  font-weight: 700;
  text-align: center;
}
.sixth-bill-date {
  color: #fff;
  text-transform: uppercase;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 15px 35px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
}
.sixth-bill::after {
  content: 'Блог';
  display: block;
  width: 75px;
  height: 30px;
  position: absolute;
  top: 15px;
  right: -5px;
  background: #b98348;
  color: #fff;
  font-size: 16px;
  font-style: italic;
  text-align: center;
  line-height: 1.7;
}
@media (max-width: 1199px) {
  .range__btn {
    font-size: 11px;
    padding: 0;
  }
  .range__bill-item {
    width: 49.5%;
  }
}
@media (max-width: 767px) {
  .range__bill-item {
    width: 100%;
  }
  .range__btn {
    font-size: 10px;
  }
  .range__list {
    flex-wrap: wrap;
  }
}
.terms {
  background: url("../img/jpg/terms.jpg") no-repeat;
  background-size: cover;
  height: 415px;
}
.terms-wrap {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.terms-wrap__paragraph,
.terms-wrap__last-text {
  color: #fff;
  text-align: center;
}
.terms-wrap__last-text {
  padding: 30px 0;
  position: relative;
  font-family: serif;
  font-style: italic;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 1px;
  display: block;
}
.terms-wrap__last-text::before {
  content: '';
  display: block;
  width: 265px;
  height: 2px;
  position: absolute;
  top: 0;
  right: -5px;
  background: #fff;
}
.terms-wrap__last-text::after {
  content: '';
  display: block;
  width: 215px;
  height: 20px;
  position: absolute;
  bottom: -20px;
  right: 0;
  background: url("../img/png/Layer 24.png") no-repeat;
  transform: translateX(-8%);
}
.terms-wrap__title {
  font-family: serif;
  font-style: italic;
  font-weight: 700;
  font-size: 27px;
  letter-spacing: 1.5px;
  line-height: 40px;
}
.terms-wrap__text,
.terms-wrap__end {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .terms {
    height: 350px;
  }
  .terms-wrap__text,
  .terms-wrap__end {
    font-size: 15px;
  }
  .terms-wrap__title {
    font-size: 17px;
  }
}
.partners {
  background: repeating-linear-gradient(125deg, rgba(195, 143, 102, 0.3), transparent, rgba(195, 143, 102, 0.3) 4px), #1f1f1f;
  background-size: cover;
  height: 150px;
}
.partners-wrap {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
@media (max-width: 1199px) {
  .partners-wrap img {
    width: 65%;
    margin: auto;
    display: block;
  }
}
@media (max-width: 767px) {
  .partners {
    height: auto;
  }
  .partners-wrap {
    flex-wrap: wrap;
    padding: 15px 0;
    justify-content: space-around;
  }
  .partners-wrap li {
    width: 50%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.form-wrap {
  height: 620px;
  position: relative;
  z-index: 1;
}
.form-wrap::before,
.form-wrap::after {
  content: '';
  width: 100%;
  height: 50%;
  display: block;
  position: absolute;
  z-index: -1;
}
.form-wrap::before {
  background: repeating-linear-gradient(125deg, rgba(195, 143, 102, 0.3), transparent, rgba(195, 143, 102, 0.3) 4px);
  top: 0;
}
.form-wrap::after {
  background: #eeeeee;
  bottom: 0;
}
.form-wrap__block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}
.form-wrap__title {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 26px;
  text-transform: uppercase;
  margin: 0;
}
.form-wrap__desc {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  font-size: 20px;
  position: relative;
  margin-bottom: 70px;
}
.form-wrap__desc::after {
  content: '';
  width: 160px;
  height: 2px;
  display: block;
  background: #1f1f1f;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -30px;
}
@media (max-width: 1199px) {
  .form-wrap {
    padding: 25px 0;
    height: auto;
  }
}
@media (max-width: 767px) {
  .form-wrap__title {
    font-size: 19px;
  }
  .form-wrap__desc {
    font-size: 15px;
    text-align: center;
  }
}
.form {
  height: 335px;
  width: 100%;
  border: 2px solid #000;
  background: #fff;
  padding: 15px 45px 45px;
}
.form__wrapper {
  width: 620px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.form-wrap--day {
  width: 165px;
}
.form-wrap--time {
  width: 110px;
}
.form-wrap--address,
.form-wrap--phone {
  width: 275px;
}
.form__fieldset {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.form__fieldset:first-child {
  width: 310px;
}
.form__fieldset:nth-child(2) {
  width: 275px;
}
.form select {
  width: 100%;
  background-size: 10px;
  color: #bd8b53;
  outline: none;
  padding: 12px 10px 12px 35px;
  font-size: 13px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  border: 3px solid #000;
}
.form input {
  width: 100%;
  color: #bd8b53;
  outline: none;
  padding: 12px 10px 12px 35px;
  font-size: 13px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  border: 3px solid #000;
}
.form select::placeholder,
.form input::placeholder {
  color: #bd8b53;
}
.form-wrap--name {
  width: 100%;
}
input.form__submit {
  width: 100%;
  background: #b98348;
  color: #fff;
  text-transform: uppercase;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 2px;
  border: none;
  border-bottom: 3px solid #8b5c28;
  padding: 20px;
  margin-top: 35px;
  outline: none;
  cursor: pointer;
}
#day {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/date.png") no-repeat 10px;
}
#address {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/geo.png") no-repeat 10px;
}
#time {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/time.png") no-repeat 10px;
}
#name {
  background: url("../img/png/user.png") no-repeat 10px;
}
#phone {
  background: url("../img/png/mobile.png") no-repeat 10px;
}
@media (max-width: 1199px) {
  .form {
    height: auto;
  }
  .form__wrapper {
    display: block;
    width: 100%;
  }
  .form__fieldset:first-child,
  .form__fieldset:nth-child(2) {
    width: 100%;
  }
  .form-wrap--day,
  .form-wrap--time,
  .form-wrap--address,
  .form-wrap--phone {
    width: 48%;
  }
}
@media (max-width: 767px) {
  .form-wrap--address,
  .form-wrap--day,
  .form-wrap--phone,
  .form-wrap--time {
    width: 100%;
  }
  .form {
    padding: 15px 20px 45px;
  }
  input.form__submit {
    font-size: 14px;
  }
  .form select::placeholder,
  .form input::placeholder,
  .form select {
    font-size: 12px;
  }
}
.footer {
  background: #f6f6f6;
  border: 1px solid #c3c3c3;
  height: 250px;
}
.footer__wrap {
  display: flex;
  height: 100%;
}
.footer ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.footer__first-list {
  padding: 50px 65px 50px 0;
  height: 100%;
  border-right: 1px solid #c3c3c3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__first-list .footer__link {
  color: #b98348;
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 14px;
  display: block;
}
ul.footer__second-list {
  padding: 50px 90px 20px 75px;
  width: 450px;
  border-right: 1px solid #c3c3c3;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.footer__title {
  margin: 0 auto 20px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;
}
.footer__second-list-link {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #4c4c4c;
  text-transform: uppercase;
  margin-bottom: 17px;
  display: block;
  width: 165px;
}
.footer__contact-block {
  padding: 50px 0 35px 35px;
}
.footer__list {
  display: flex;
  list-style: none;
  padding: 0;
  width: 130px;
  height: 35px;
  border: 1px solid #b98348;
}
.footer__item {
  width: 25%;
  height: 100%;
}
.footer__item:not(:last-child) {
  border-right: 1px solid #b98348;
}
.footer__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.footer__link svg {
  height: 15px;
}
.footer__address {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
}
.footer__address address {
  font-style: normal;
  display: inline;
  padding-left: 5px;
}
.footer__phone {
  display: block;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
}
.footer__phone-link {
  font-weight: 700;
  color: #1f1f1f;
  padding-left: 5px;
  text-decoration: none;
}
.footer__mail {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  margin-bottom: 40px;
  display: block;
}
.footer__mail-link {
  font-weight: 700;
  color: #b98348;
  padding-left: 5px;
  text-decoration: none;
  border-bottom: 1px solid #b98348;
}
@media (max-width: 1199px) {
  .footer {
    height: auto;
  }
  .footer__first-list {
    padding: 50px 0 50px 0;
  }
  .footer__first-list .footer__link {
    width: 140px;
  }
  ul.footer__second-list {
    padding: 50px 50px 20px 60px;
    width: 275px;
  }
}
@media (max-width: 767px) {
  .footer__wrap {
    display: block;
  }
  .footer__first-list,
  ul.footer__second-list {
    border: none;
    justify-content: space-around;
  }
  .footer__first-list,
  ul.footer__second-list {
    padding: 25px 0;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .footer__first-list li,
  .footer__second-list li {
    width: 50%;
    text-align: center;
  }
  .footer__second-list-link,
  .footer__first-list .footer__link {
    font-size: 12px;
    width: auto;
    margin-bottom: 12px;
  }
  .footer__first-list,
  .footer__second-list {
    position: relative;
  }
  .footer__first-list::after,
  .footer__second-list::after {
    content: '';
    display: block;
    background: -webkit-gradient(linear, left top, right top, from(#fff), color-stop(#000), to(#fff));
    background: linear-gradient(to right, #fff, #000, #fff);
    border: 0;
    height: 1px;
    width: 100%;
    margin: auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .footer__title {
    font-size: 12px;
  }
  .footer__contact-block {
    padding: 25px 0;
  }
  ul.footer__list {
    margin: auto;
  }
  .footer__address {
    display: block;
  }
  .footer__address,
  .footer__title,
  .footer__mail,
  .footer__phone {
    text-align: center;
  }
}
