@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Regular.woff2"),
  url("../fonts/ProximaNova-Regular.woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Semibold.woff2"),
  url("../fonts/ProximaNova-Semibold.woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Bold.woff2"),
  url("../fonts/ProximaNova-Bold.woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-Extrabld.woff2"),
  url("../fonts/ProximaNova-Extrabld.woff");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Proxima";
  src: url("../fonts/ProximaNova-RegularIt.woff2"),
  url("../fonts/ProximaNova-RegularIt.woff");
  font-weight: 400;
  font-style: italic;
}


* {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

label {
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 13px;
  margin: 30px auto 10px;
  display: block;
}

select {
  appearance: none;
  background: url('../img/svg/arrow.svg') no-repeat 95%;
  outline: 0;
}

.copyright {
  background: #eeeeee;
  padding: 12px 0;
  color: #9d9c9c;
  font-family: "Proxima", Arial, sans-serif;
}

@media (max-width: 767px) {
  .copyright {
    padding-bottom: 65px;
    font-size: 12px;
  }
}

