.top-wrap__container {
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: space-between;
}

.top-wrap__list {
  display: flex;
  list-style: none;
  padding: 0;
  width: 130px;
  height: 35px;
  border: 2px solid #c2c2c2;
}


.top-wrap__item {
  width: 25%;
  height: 100%;
}

.top-wrap__item:not(:last-child) {
  border-right: 2px solid #c2c2c2;
}

.top-wrap__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.top-wrap__link svg {
  height: 15px;
}

.top-wrap__city {
  border: none;
  appearance: none;
  background: url(../img/svg/arrow.svg) no-repeat right;
  background-size: 14%;
  width: 85px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  color: #1f1f1f;
  outline: none;
}

.top-wrap__city-wrap,
.top-wrap__phone-wrap,
.top-wrap__user-wrap,
.top-wrap__basket-wrap {
  position: relative;
  padding-left: 20px;
}

.top-wrap__city-wrap svg,
.top-wrap__phone-wrap svg,
.top-wrap__user-wrap svg,
.top-wrap__basket-wrap svg {
  position: absolute;
  height: 16px;
  left: 0;
  top: 1px;
}

.top-wrap__phone,
.top-wrap__user,
.top-wrap__basket {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  color: #1f1f1f;
  text-decoration: none;
}

.top-wrap__user:hover,
.top-wrap__basket:hover {
  border-bottom: 1px dashed #888888;
}

.top-wrap__basket-wrap {
  position: relative;
  padding-right: 35px;
}

.top-wrap__basket-wrap::after {
  content: '3';
  display: block;
  width: 15px;
  height: 20px;
  color: #bd8b53;
  font-family: 'PTSerif', serif;
  font-size: 18px;
  border-left: 1px solid #bd8b53;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 5px;
}

@media (max-width: 1199px) {
  .top-wrap__city,
  .top-wrap__phone,
  .top-wrap__user,
  .top-wrap__basket {
    font-size: 14px;
  }

  .top-wrap__city {
    width: 70px;
  }
}

@media (max-width: 767px) {
  .top-wrap {
    position: fixed;
    bottom: 0;
    background: #fff;
    width: 100%;
    z-index: 5;
    border-top: 1px solid #000;
  }

  .top-wrap__list,
  .top-wrap__phone-wrap {
    display: none;
  }

  .top-wrap__basket, .top-wrap__city, .top-wrap__phone, .top-wrap__user {
    font-size: 12px;
  }
}
