.header {
  background: #1f1f1f;
  height: 60px;
}

.header__wrap,
.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__nav-list {
  padding: 0;
  list-style: none;
  width: 700px;
}

.header__nav-link {
  color: #fff;
  text-transform: uppercase;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 17px;
  text-decoration: none;
}

.header__nav-link:hover {
  border-bottom: 1px dashed #888888;
}

@media (max-width: 1199px) {
  .header__wrap img {
    width: 100px;
  }

  .header__nav-link {
    font-size: 12px;
  }

  .header__nav-list {
    width: 470px;
  }
}

@media (max-width: 767px) {
  .header__wrap {
    display: block;
  }

  .header__wrap > a {
    display: block;
    margin: auto;
    width: 110px;
  }

  .header__nav-link {
    font-size: 10px;
  }

  .header {
    height: auto;
    padding: 10px 0;
  }

  .header__nav-list {
    width: 100%;
    margin: 15px 0 0;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }

  .header__nav-list li {
    width: 50%;
  }
}
