.footer {
  background: #f6f6f6;
  border: 1px solid #c3c3c3;
  height: 250px;
}

.footer__wrap {
  display: flex;
  height: 100%;
}

.footer ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.footer__first-list {
  padding: 50px 65px 50px 0;
  height: 100%;
  border-right: 1px solid #c3c3c3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.footer__first-list .footer__link {
  color: #b98348;
  text-transform: uppercase;
  text-decoration: none;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 14px;
  display: block;
}

ul.footer__second-list {
  padding: 50px 90px 20px 75px;
  width: 450px;
  border-right: 1px solid #c3c3c3;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.footer__title {
  margin: 0 auto 20px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;
}

.footer__second-list-link {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #4c4c4c;
  text-transform: uppercase;
  margin-bottom: 17px;
  display: block;
  width: 165px;
}

.footer__contact-block {
  padding: 50px 0 35px 35px;
}

.footer__list {
  display: flex;
  list-style: none;
  padding: 0;
  width: 130px;
  height: 35px;
  border: 1px solid #b98348;
}

.footer__item {
  width: 25%;
  height: 100%;
}

.footer__item:not(:last-child) {
  border-right: 1px solid #b98348;
}

.footer__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.footer__link svg {
  height: 15px;
}

.footer__address {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
}

.footer__address address {
  font-style: normal;
  display: inline;
  padding-left: 5px;
}

.footer__phone {
  display: block;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 14px;
}

.footer__phone-link {
  font-weight: 700;
  color: #1f1f1f;
  padding-left: 5px;
  text-decoration: none;
}

.footer__mail {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  margin-bottom: 40px;
  display: block;
}

.footer__mail-link {
  font-weight: 700;
  color: #b98348;
  padding-left: 5px;
  text-decoration: none;
  border-bottom: 1px solid #b98348;
}

@media (max-width: 1199px) {
  .footer {
    height: auto;
  }

  .footer__first-list {
    padding: 50px 0 50px 0;
  }

  .footer__first-list .footer__link {
    width: 140px;
  }

  ul.footer__second-list {
    padding: 50px 50px 20px 60px;
    width: 275px;
  }
}

@media (max-width: 767px) {
  .footer__wrap {
    display: block;
  }

  .footer__first-list, ul.footer__second-list {
    border: none;
    justify-content: space-around;
  }

  .footer__first-list,
  ul.footer__second-list {
    padding: 25px 0;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .footer__first-list li,
  .footer__second-list li {
    width: 50%;
    text-align: center;
  }

  .footer__second-list-link,
  .footer__first-list .footer__link {
    font-size: 12px;
    width: auto;
    margin-bottom: 12px;
  }


  .footer__first-list,
  .footer__second-list {
    position: relative;
  }

  .footer__first-list::after,
  .footer__second-list::after {
    content: '';
    display: block;
    background: -webkit-gradient(linear, left top, right top, from(#fff), color-stop(#000), to(#fff));
    background: linear-gradient(to right, #fff, #000, #fff);
    border: 0;
    height: 1px;
    width: 100%;
    margin: auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .footer__title {
    font-size: 12px;
  }

  .footer__contact-block {
    padding: 25px 0;
  }

  ul.footer__list {
    margin: auto;
  }

  .footer__address {
    display: block;
  }

  .footer__address,
  .footer__title,
  .footer__mail,
  .footer__phone {
    text-align: center;
  }

}
