.partners {
  background: repeating-linear-gradient(125deg, rgba(195, 143, 102, .3), transparent, rgba(195, 143, 102, .3) 4px), #1f1f1f;
  background-size: cover;
  height: 150px;
}

.partners-wrap {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

@media (max-width: 1199px) {
  .partners-wrap img {
    width: 65%;
    margin: auto;
    display: block;
  }
}

@media (max-width: 767px) {
  .partners {
    height: auto;
  }

  .partners-wrap {
    flex-wrap: wrap;
    padding: 15px 0;
    justify-content: space-around;
  }

  .partners-wrap li {
    width: 50%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }


}
