.form {
  height: 335px;
  width: 100%;
  border: 2px solid #000;
  background: #fff;
  padding: 15px 45px 45px;
}

.form__wrapper {
  width: 620px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form-wrap--day {
  width: 165px;
}

.form-wrap--time {
  width: 110px;
}

.form-wrap--address,
.form-wrap--phone {
  width: 275px;
}

.form__fieldset {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form__fieldset:first-child {
  width: 310px;
}

.form__fieldset:nth-child(2) {
  width: 275px;
}

.form select {
  width: 100%;
  background-size: 10px;
  color: #bd8b53;
  outline: none;
  padding: 12px 10px 12px 35px;
  font-size: 13px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  border: 3px solid #000;
}

.form input {
  width: 100%;
  color: #bd8b53;
  outline: none;
  padding: 12px 10px 12px 35px;
  font-size: 13px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  border: 3px solid #000;
}

.form select::placeholder,
.form input::placeholder {
  color: #bd8b53;
}

.form-wrap--name {
  width: 100%;
}

input.form__submit {
  width: 100%;
  background: #b98348;
  color: #fff;
  text-transform: uppercase;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 2px;
  border: none;
  border-bottom: 3px solid #8b5c28;
  padding: 20px;
  margin-top: 35px;
  outline: none;
  cursor: pointer;
}

#day {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/date.png") no-repeat 10px;
}

#address {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/geo.png") no-repeat 10px;
}

#time {
  background: url('../img/png/arrow.png') no-repeat 95%, url("../img/png/time.png") no-repeat 10px;
}

#name {
  background: url("../img/png/user.png") no-repeat 10px;
}

#phone {
  background: url("../img/png/mobile.png") no-repeat 10px;
}

@media (max-width: 1199px) {
  .form {
    height: auto;
  }

  .form__wrapper {
    display: block;
    width: 100%;
  }

  .form__fieldset:first-child,
  .form__fieldset:nth-child(2) {
    width: 100%;
  }

  .form-wrap--day,
  .form-wrap--time,
  .form-wrap--address,
  .form-wrap--phone {
    width: 48%;
  }
}

@media (max-width: 767px) {
  .form-wrap--address, .form-wrap--day, .form-wrap--phone, .form-wrap--time {
    width: 100%;
  }

  .form {
    padding: 15px 20px 45px;
  }

  input.form__submit {
    font-size: 14px;
  }

  .form select::placeholder,
  .form input::placeholder,
  .form select {
    font-size: 12px;
  }
}
