.range__list,
.range__bill-list {
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.range__btn {
  border: 2px solid transparent;
  background: transparent;
  font-family: "Proxima";
  font-weight: 800;
  font-size: 14px;
  cursor: pointer;
  padding: 15px;
  text-transform: uppercase;
  height: 45px;
}

.range__btn:hover {
  color: #b98348;
  border: 2px solid #b98348;
}

.range__bill-list {
  flex-wrap: wrap;
}

.range__bill-item {
  width: 33%;
  height: 328px;
  margin-bottom: 5px;
}

.range__bill-item article {
  width: 100%;
  height: 100%;
  background-size: cover;
}

.range__bill-item:nth-child(1) article {
  background: url("../img/jpg/range1.jpg") no-repeat;
}

.range__bill-item:nth-child(2) article {
  background: url("../img/jpg/range2.jpg") no-repeat;
}

.range__bill-item:nth-child(3) article {
  background: url("../img/jpg/range3.jpg") no-repeat;
}

.range__bill-item:nth-child(4) article {
  background: url("../img/jpg/range4.jpg") no-repeat;
}

.range__bill-item:nth-child(5) article {
  background: url("../img/jpg/range5.jpg") no-repeat;
}

.range__bill-item:nth-child(6) article {
  background: url("../img/jpg/range6.jpg") no-repeat;
}

.first-bill-header,
.first-bill-desc,
.first-bill-text,
.fourth-bill-text {
  font-family: "Proxima", Arial, sans-serif;
  color: #fff;
  text-transform: uppercase;
}

.first-bill-title {
  text-align: center;
  position: absolute;
  top: 50%;
  margin: 0;
}

.first-bill-header {
  font-weight: 700;
  font-size: 22px;
}

.first-bill-desc {
  font-size: 22px;
  font-weight: 400;
  font-family: serif;
}

.first-bill-text {
  font-size: 12px;
  margin-top: 20px;
  display: block;
}

.first-bill,
.second-bill,
.third-bill,
.fourth-bill,
.fifth-bill,
.sixth-bill {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
}

.third-bill-text {
  color: #fff;
  background: rgba(195, 143, 102, 0.6);
  padding: 15px 20px;
  width: 90%;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
}

.third-bill-text {
  position: absolute;
  top: 40%;
}

.fourth-bill {
  flex-direction: column;
}

.fourth-bill-text {
  text-align: center;
  font-size: 22px;
}

.fourth-bill-italic {
  font-family: serif;
  font-weight: 400;
  margin-top: 15px;
  display: block;
}

.fourth-bill-decor {
  position: relative;
}

.fourth-bill-decor::before,
.fourth-bill-decor::after {
  content: '';
  display: block;
  position: absolute;
  width: 40px;
  height: 1px;
  background: #fff;
  top: 50%;
}

.fourth-bill-decor::before {
  left: -45px;
}

.fourth-bill-decor::after {
  right: -45px;
}

.fourth-bill-link {
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border: 2px solid #fff;
  padding: 20px;
}

.fifth-bill-desc {
  text-transform: uppercase;
  color: #fff;
  font-family: 'Proxima', Arial, sans-serif;
  font-weight: 700;
  text-align: center;
}

.fifth-bill-text {
  font-size: 22px;
}

.fifth-bill-name-text {
  font-family: Serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 2;
}

.fifth-bill-last-text {
  font-size: 12px;
}

.sixth-bill-text {
  color: #fff;
  text-transform: uppercase;
  font-size: 22px;
  font-family: 'Proxima', Arial, sans-serif;
  font-weight: 700;
  text-align: center;
}

.sixth-bill-date {
  color: #fff;
  text-transform: uppercase;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 15px 35px;
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 700;
  font-size: 16px;
}

.sixth-bill::after {
  content: 'Блог';
  display: block;
  width: 75px;
  height: 30px;
  position: absolute;
  top: 15px;
  right: -5px;
  background: #b98348;
  color: #fff;
  font-size: 16px;
  font-style: italic;
  text-align: center;
  line-height: 1.7;
}

@media (max-width: 1199px) {
  .range__btn {
    font-size: 11px;
    padding: 0;
  }

  .range__bill-item {
    width: 49.5%;
  }
}

@media (max-width: 767px) {
  .range__bill-item {
    width: 100%;
  }

  .range__btn {
    font-size: 10px;
  }

  .range__list {
    flex-wrap: wrap;
  }
}
