.terms {
  background: url("../img/jpg/terms.jpg") no-repeat;
  background-size: cover;
  height: 415px;
}

.terms-wrap {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.terms-wrap__paragraph,
.terms-wrap__last-text {
  color: #fff;
  text-align: center;
}

.terms-wrap__last-text {
  padding: 30px 0;
  position: relative;
  font-family: serif;
  font-style: italic;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 1px;
  display: block;
}

.terms-wrap__last-text::before {
  content: '';
  display: block;
  width: 265px;
  height: 2px;
  position: absolute;
  top: 0;
  right: -5px;
  background: #fff;
}

.terms-wrap__last-text::after {
  content: '';
  display: block;
  width: 215px;
  height: 20px;
  position: absolute;
  bottom: -20px;
  right: 0;
  background: url("../img/png/Layer 24.png") no-repeat;
  transform: translateX(-8%);
}

.terms-wrap__title {
  font-family: serif;
  font-style: italic;
  font-weight: 700;
  font-size: 27px;
  letter-spacing: 1.5px;
  line-height: 40px;
}

.terms-wrap__text,
.terms-wrap__end {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
}


@media (max-width: 767px) {
  .terms {
    height: 350px;
  }

  .terms-wrap__text,
  .terms-wrap__end {
    font-size: 15px;
  }

  .terms-wrap__title {
    font-size: 17px;
  }
}

