.greetings {
  height: 450px;
  background: url("../img/jpg/greetings.jpg") no-repeat;
  background-size: cover;
}

.greetings__slogan {
  font-family: "Proxima";
  font-weight: 700;
  font-size: 48px;
  color: #fff;
  margin: 0;
  text-align: center;
  padding: 30px 0;
  position: relative;
}

.greetings__slogan::before,
.greetings__slogan::after {
  content: '';
  width: 165px;
  height: 4px;
  background: #fff;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.greetings__slogan::before {
  top: 0;
}

.greetings__slogan::after {
  bottom: 0;
}

.greetings__slogan--small {
  font-family: "Proxima";
  font-weight: 400;
}

.greetings__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  position: relative;
}

.greetings__pic {
  position: absolute;
  right: 0;
  bottom: 0;
}


@media (max-width: 1199px) {
  .greetings__slogan {
    z-index: 1;
  }

  .greetings__pic {
    right: -25px;
  }
}


@media (max-width: 767px) {
  .greetings__slogan {
    font-size: 25px;
    filter: drop-shadow(0px 0px 2px #000);
  }
  .greetings {
    height: 300px;
  }

  .greetings__pic {
    width: 90%;
    right: 0;
  }
}
