.form-wrap {
  height: 620px;
  position: relative;
  z-index: 1;
}

.form-wrap::before,
.form-wrap::after {
  content: '';
  width: 100%;
  height: 50%;
  display: block;
  position: absolute;
  z-index: -1;
}

.form-wrap::before {
  background: repeating-linear-gradient(125deg,rgba(195,143,102,.3),transparent,rgba(195,143,102,.3) 4px);
  top: 0;
}

.form-wrap::after {
  background: #eeeeee;
  bottom: 0;
}

.form-wrap__block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.form-wrap__title {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 600;
  font-size: 26px;
  text-transform: uppercase;
  margin: 0;
}

.form-wrap__desc {
  font-family: "Proxima", Arial, sans-serif;
  font-weight: 400;
  font-size: 20px;
  position: relative;
  margin-bottom: 70px;
}

.form-wrap__desc::after {
  content: '';
  width: 160px;
  height: 2px;
  display: block;
  background: #1f1f1f;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -30px;
}

@media (max-width: 1199px) {
  .form-wrap {
    padding: 25px 0;
    height: auto;
  }
}

@media (max-width: 767px) {
  .form-wrap__title {
    font-size: 19px;
  }

  .form-wrap__desc {
    font-size: 15px;
    text-align: center;
  }
}
